<template>
    <div>
        <div class="app">
            <!--  广告入口  -->
            <div class="ad" @click="randomRed">
                <img src="@/static/images/hbgroup/hongbao_banner.png" alt="">
            </div>
            <div class="hbTitle"><router-link to="/redstrategy"> <img src="@/static/images/hbgroup/gonglue_icon.png" alt=""><span>玩转红包群，日赚500的官方攻略！</span></router-link></div>
            <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text=""
                    @load="getList"
                    v-if="listData.length"
            >
            <ul class="list">
                <li v-for="(item,index) in listData">
                    <router-link :class="{tag:item.creater}" :to="{path:'/redchat', query:{groupId:item.id, order:index+1}}">
                        <img src="@/static/images/hbgroup/redicon1.png" alt />
                        <p>{{item.name}}</p>
                        <p>{{item.money}}个币{{item.count}}个包</p>
                        <p v-if="item.send_rule == 1">最小的发</p>
                        <p v-else>最大的发</p>
                        <p>群人数{{item.user_count}}</p>
                        <p>使用币种{{item.currency}}</p>
                    </router-link>
                </li>
            </ul>
            </van-list>
            <div v-if="!listData.length" class="searchresult">
                <img src="@/static/images/tab/wujilv_image.png" alt="">
                <p>无记录, 赶快去收藏~</p>
            </div>
        </div>
        <tab-bar></tab-bar>
    </div>


</template>

<script>
    import tabBar from '@/components/tabBar'
    import { redList, next } from '@/api/red'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style'
    import { List } from "vant"
    import 'vant/lib/list/style'
    import fmath from '@/utils/math'
    export default {
        name: "redgroup",
        components: {
            tabBar,
            [List.name]: List,
        },
        data()
        {
            return {
                loading:false,
                finished:false,
                page:1,
                listData:[],
                orderText:['0', '一', '二', '三', '四', '五', '六']
            }
        },
        mounted()
        {
            //this.getList()
        },
        methods:{

            // 我的财富树列表
            getList()
            {
                this.loading = true
                redList({page:this.page}).then(res => {
                    this.loading = false
                    if (this.page == 1)
                    {
                        this.listData = [];
                    }
                    this.page++
                    if (res.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res)

                })
            },
            random()
            {
                return fmath.random(1, 4)
            },
            randomRed()
            {
                next({type:'rand'}).then(res => {

                    this.$router.push('/redchat?groupId='+res.redgroup_id+'&type=rand')
                })
            }

        },

        // 数据暂时不用缓存信息
        activated(){
            this.page = 1;
            this.getList()
        }
    }
</script>

<style scoped>
    @import "../../static/css/redgroup.css";
    .searchresult {
        height: 70vh;
        text-align: center;
    }
    .searchresult img {
        width: 1.1rem;
        height: 1rem;
        display: block;
        margin: auto;
        padding-top: 1rem;
    }
    .searchresult p {
        font-size: 0.14rem;
        color: #666666;
        margin-top: 0.2rem;
    }
    .app .list li a p:nth-child(6) {
        font-size: 0.11rem;
        color: #404040;
        margin-top: 0.04rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 0 0.04rem;
    }
</style>



